/*
    mind: i - e
    energy: s - n
    nature: f - t
    tactics: p - j
    identity: a - t
*/

export function getTypeOptions(type)
{
    const chars = Array.from(type);
    return { mind: chars[0], energy: chars[1], nature: chars[2], tactics: chars[3] };
}

export function isValidType(type)
{
    return TYPES.hasOwnProperty(type);
}

